.root {
    height: 100%;
    background-color: #fff;
}

.navHeader {
    background-color: #21b97a;
}

.backHome {
    margin-top: 30px;
    text-align: center;
}

.backHome :global(a) {
    color: #666;
}

.formItem {
    position: relative;
    margin-bottom: 9px;
}

.formItem::after {
    content: '';
    position: absolute;
    background-color: #ddd;
    display: block;
    z-index: 1;
    top: auto;
    right: auto;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    -webkit-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}

.input {
    height: 60px;
    width: 100%;
    padding: 2px 0;
    border: 0;
    color: #000;
    font-size: 17px;
    background-color: transparent;
}

.input::placeholder {
    color: #c0c0c0;
}

.formSubmit {
    margin-top: 27px;
}

.submit {
    display: block;
    outline: 0 none;
    -webkit-appearance: none;
    padding: 0;
    text-align: center;
    font-size: 18px;
    height: 50px;
    line-height: 50px;
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: break-word;
    white-space: nowrap;
    color: #fff;
    background-color: rgb(28, 182, 118);
    border: 1px solid rgb(28, 182, 118);
    border-radius: 2px;
    width: 100%;
}

.error {
    color: red;
}